.edit-avatar {
  border: none;
  margin: 0;
  padding: 0;
}

.edit-avatar__input {
  display: none;
}

.edit-avatar__label {
  cursor: pointer;
  margin-bottom: 21px;
  display: inline-block;
  position: relative;
}

.edit-avatar__label:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: #00000080;
  border-radius: 50%;
  transition: opacity .3s;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.edit-avatar__label:hover:before, .edit-avatar__label:hover > .edit-avatar__span {
  opacity: 1;
}

.edit-avatar__span {
  opacity: 0;
  color: #fff;
  font-size: 13px;
  font-weight: 500;
  line-height: 14px;
  transition: opacity .3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

